	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Halftime Report","date":"27 Apr 2001","slug":"/2001-04-27-halftime-report/","tags":"Y2001"},"html":"<p><b>He's walking</b><br />\nNot just \"two steps to get from chair to chair because it's too much trouble to get down and crawl\", this is \"get up by myself and take six steps just because I feel like it\". And Grandma and Grandpa were here to see it. The world has just become a tiny bit more unsafe.</p>\n<p><b>Early</b><br />\nGrandma and Grandpa have been here a week. Mama's had a busy work week, so Christopher and I have done our best to entertain them.</p>\n<p>No pictures, because we've been playing with a new Canon MV300i video camera. I've knocked myself out trying to load video into the PC. I think the Firewire card is bad.</p>"},"next":{"fields":{"title":"Grandma and Grandpa","date":"20 Apr 2001","slug":"/2001-04-20-grandma-and-grandpa/"}},"previous":{"fields":{"title":"First Steps","date":"01 May 2001","slug":"/2001-05-01-first-steps/"}}},{"node":{"fields":{"title":"Grandma and Grandpa","date":"20 Apr 2001","slug":"/2001-04-20-grandma-and-grandpa/","tags":"Y2001"},"html":"<p>Grandma and Grandpa will be arriving from Minnesota today and staying for 2-1/2 weeks. Updates to PapaScott will then be even more sporadic than usual</p>"},"next":{"fields":{"title":"White Easter","date":"15 Apr 2001","slug":"/2001-04-15-white-easter/"}},"previous":{"fields":{"title":"Halftime Report","date":"27 Apr 2001","slug":"/2001-04-27-halftime-report/"}}},{"node":{"fields":{"title":"White Easter","date":"15 Apr 2001","slug":"/2001-04-15-white-easter/","tags":"Y2001"},"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2001/04/oma.jpg\" height=\"243\" width=\"325\" border=\"0\" alt=\"oma.jpg: \" /></p>\n<p><b>Late</b><br />\nLuckily, it didn't stay cold the whole day. We went to visit Christopher's grandmother in Eckernförde, and while the weather wasn't particularly pleasant, it wasn't cold. Christopher was in a better mood than he looks like in the pictureand yes, he has a new haircut.</p>\n<p><b>Early</b><br />\nDid I mention that it's cold here? It's snowing on Easter morning!</p>"},"next":{"fields":{"title":"Easter Fire 2001","date":"14 Apr 2001","slug":"/2001-04-14-easter-fire-2001/"}},"previous":{"fields":{"title":"Grandma and Grandpa","date":"20 Apr 2001","slug":"/2001-04-20-grandma-and-grandpa/"}}}],"pathPrefix":"","first":false,"last":false,"index":1008,"pageCount":1101}}

